@import "var";

body{
  background-color: lightgray;
  font-family: Roboto, sans-serif;
}

.mainContainer{
  width: 80%;
  min-height: 100vh;
  background-color: whitesmoke;
  margin: 20px auto;
  padding: 12px;
}
.flex{
  display: flex;
  flex-wrap: wrap;
  align-items: start;
  justify-content: space-between;
}
.flexWrap{
  display: flex;
  flex-wrap: wrap;
}

.cover {
  object-fit: cover;
}
.container{
  width: 100%;
  margin: 0 0 20px 0 ;
}
.container h1{
  font-size: 2em;
  color: $blue;
}
.serv{
  max-width: 250px;
  box-shadow: 0 1px 6px rgba(0,0,0,.1);
}
.serv img{
  max-width: 250px;
  max-height: 200px;
}
.servName{
  background-color: $blue;
  color: white;
  width: 100% ;
  padding: 5px;
}