@import 'var';

.bannerWhite {
  width: 100%;
  background-color: whitesmoke;
  display: flex;
  justify-content: center;
  position: relative;
  box-shadow: 0 1px 6px rgba(0,0,0,.1);
}

.logo{
    width: 100px;
  }

.navBlock{
  min-width: 80%;
  display: flex;
  justify-content: space-evenly;
  & ul{
    display: flex;
    list-style: none;
  }
}
.navItem{
  display: flex;
  align-items: center;
}
.navItem a{
  text-decoration: none;
  padding-right: 2em;
  padding-left: 2em;
  color: $blue;
  font-family: Lato;
  font-size: 20px;
  text-align: center;
}

.navItem :hover {
  color: $blueFresh;
}

.ConnectBtn {
  max-width: 80px;
  max-height: 40px;
  background-color: $blue;
  font-family: Lato;
  border-radius: 5px;
  display: flex;
  position: absolute;
  justify-content: center;
  padding: 5px;
  color: whitesmoke;
  text-decoration:none;
  text-align: center;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}